@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  background-color: #36f3ca;
  color: #333;
  font-size: 10px;
}

.App {
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}
.title {
  font-size: 4.4rem;
  font-family: "Playfair", sans-serif;
}
.form {
  display: grid;
}
.label {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.input {
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  background-color: #f24540;
  margin-bottom: 1rem;
  color: #ffffff;
}
.button {
  background-color: #ffe43c;
  color: #000;
  padding: 1rem 2rem;
  border: 1px solid rgba(0, 0, 0, 0.75);
  border-radius: 20px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: #110d09;
  color:#ffffff;
}
.card-list {
  -webkit-column-count: 3;
          column-count: 3;
}
.card {
  margin-bottom: 1rem;
  display: -webkit-flex;
  display: flex;
}

.card--image {
  -webkit-flex: 100% 1;
          flex: 100% 1;
  margin-top: 1rem;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .form {
    grid-template-columns: auto 1fr auto;
    grid-gap: 1rem;
    -webkit-align-items: center;
            align-items: center;
  }
  .input {
    margin-bottom: 0;
  }
}

@media only screen and (max-width: 600px) {
    .card-list {
        -webkit-column-count: 1;
                column-count: 1;
    }
}
